* {
  font-family: "Open Sans", sans-serif;
}

body {
  padding: 0;
  margin: 0;
  /* border: 1px solid black; */
  display: flex;
  justify-content: space-around;
  background-color: #dcdcdc;
  color: #696969;
}

input {
  border: 1px solid #dcdcdc;
}

input[type="text"],
input[type="number"] {
  padding: 5px;
  padding-left: 10px;
  font-size: 16px;
  border-radius: 4px;
  border: 2px solid #dcdcdc;
}

input[name="checks"] {
  margin-top: 5px;
  margin-bottom: 25px;
  width: 65%;
}

input[type="text"]:focus,
input[type="number"]:focus {
  outline-color: #4285f4;
}

.form-container {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  width: 160%;
  margin-left: -100px;
  border: 1px solid #dcdcdc;
  justify-content: center;
  background-color: white;
  box-shadow: 0px 0px 10px 4px #c0c0c0;
  border-radius: 4px;
}

.form-container .head {
  width: 96%;
  padding: 10px;
  border-bottom: 1px solid #dcdcdc;
  font-weight: 500;
  color: black;
}

form {
  padding: 10px;
}

.coupon-type {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 5px;
  /* border: 1px solid green; */
}

.radio1,
.radio2 {
  padding: 5px;
  border-radius: 4px;
  border: 1px solid #dcdcdc;
  width: 45%;
  padding-top: 10px;
  padding-bottom: 10px;
}

.main-fields {
  /* border: 1px solid green; */
  padding: 5px;
  display: flex;
  flex-direction: column;
}

.name-field,
.coupon-field {
  display: flex;
  flex-direction: column;
  padding-bottom: 5px;
  padding-top: 5px;
}

.discount-type-wrapper {
  padding: 5px;
  /* border: 1px solid green; */
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.radio-wrapper {
  /* border: 1px solid green; */
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.radio-per,
.radio-amt {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 15%;
}

.checkbox-wrapper {
  /* border: 1px solid green; */
  padding: 5px;
  display: flex;
  flex-direction: column;
}

.option1,
.option2,
.option3 {
  display: flex;
  flex-direction: column;
}

.checkbox1 input {
}

.txt {
  font-size: 15px;
  font-weight: 600;
  padding-bottom: 5px;
}

.btns {
  /* border: 1px solid blue; */
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding: 5px;
}

button {
  margin-left: 20px;
  background-color: white;
  padding: 10px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  color: #696969;
  border: 2px solid #c0c0c0;
}

.submit-btn {
  color: white;
  background-color: #4285f4;
  border-color: #4285f4;
  padding-left: 15px;
  padding-right: 15px;
}
.selected-radio {
  background-color: rgba(66, 133, 244, 10%);
  border-color: #4285f4;
  color: #4285f4;
}
